.dev-icons {
    font-size: 3rem;
    width: 100%;
  }

  .port {
      text-align: center;
  }

  .portfolio {
    position: relative;
    width: 100%;
    height: 400px;
    margin-bottom: 20px;
}

  .imageText {
    position: absolute;
    bottom: 20px;
    /* the a is alpha, or the opacity level */
    background-color: rgba(0, 0, 255, 0.5); 
    padding: 10px 0px;
    width: 100%;
    text-align: center;
    margin: 0;
    color: white;
}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.reactText {
    font-size: 15px;
}

.react .reactText {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;

    position: absolute;
    z-index: 1; 
}

.react:hover .reactText {
    visibility: visible;
}

